import React from "react";
import Alert from "../alert";
import styles from "./index.module.css";

const renderPeixels = [
  [1, 1, 1],
  [1, 1, 0.8],
  [1.2, 1, 1],
  [1, 1.2, 1],
  [1, 1, 1.2],
];

export default React.memo(function Setting(props) {
  const [show, setShow] = React.useState(false);
  const [showAlert, setShowAlert] = React.useState(false);
  const [message, setMessage] = React.useState("");
  const renderPeixelIndex = React.useRef(0);
  return (
    <>
      <div
        className={styles.settingIcon}
        onClick={() => {
          props.clickSound();
          setShow(true);
        }}
      />
      <Alert
        show={showAlert}
        message={message}
        onCancel={() => setShowAlert(false)}
      />
      <div
        className={styles.modal}
        style={{ display: show ? "flex" : "none" }}
        onClick={() => setShow(false)}
      >
        <div className={styles.container}>
          <div
            className={styles.button}
            onClick={() => {
              setMessage("QQ618027271!");
              setShowAlert(true);
            }}
          >
            
          </div>
          <div
            className={styles.button}
            onClick={() => {
              setMessage("");
              setShowAlert(true);
            }}
          >
            
          </div>
          <div
            className={styles.button}
            onClick={() => {
              renderPeixelIndex.current =
                renderPeixelIndex.current < renderPeixels.length - 1
                  ? renderPeixelIndex.current + 1
                  : 0;
              window.localStorage.setItem(
                "renderPeixel",
                JSON.stringify(renderPeixels[renderPeixelIndex.current])
              );
              window.renderPeixel = renderPeixels[renderPeixelIndex.current];
            }}
          >
            
          </div>
          <div
            className={styles.button}
            onClick={() => {
              window.localStorage.setItem(
                "enableVibrate",
                window.enableVibrate ? "false" : "true"
              );
              setMessage(
                window.enableVibrate ? "" : ""
              );
              window.enableVibrate = !window.enableVibrate;
              setShowAlert(true);
            }}
          >
            
          </div>
          <div
            className={styles.button}
            onClick={() => {
              window.localStorage.setItem(
                "enableAudio",
                window.enableAudio ? "false" : "true"
              );
              setMessage(window.enableAudio ? "" : "");
              window.enableAudio = !window.enableAudio;
              setShowAlert(true);
            }}
          >
            
          </div>
          <div
            className={styles.button2}
            onClick={() => {
              setMessage(
                "RPGRPGKevinjacky14(https://gitee.com/bgwp/fmj.kt)(https://github.com/jacky14/fmj_pc)"
              );
              setShowAlert(true);
            }}
          >
            
          </div>
        </div>
      </div>
    </>
  );
});
