import React from "react";
import GameCenter from "./components/game-center";
import Alert from "./components/alert";
import Setting from "./components/setting";
import { initGame } from "./library";
import styles from "./index.module.css";

let timer;

function checkUse() {
  return (
    window.location.origin.includes("vercel") ||
    window.location.origin.includes("localhost")
  );
}

export default React.memo(function Home() {
  const [showLoading, setShowloading] = React.useState(true);
  const [loading, setloading] = React.useState("...");
  const [showGameCenter, toggleShowGameCenter] = React.useState(false);
  const [showAlert, setShowAlert] = React.useState(false);
  const audio = React.useRef(null);

  const clickSound = React.useCallback(() => {
    if (audio.current && window.enableAudio) {
      audio.current.currentTime = 0;
      audio.current.play();
    }
  }, []);

  const clickButton = React.useCallback(() => {
    try {
      clickSound();
      if (window.enableVibrate) {
        navigator.vibrate =
          navigator.vibrate ||
          navigator.webkitVibrate ||
          navigator.mozVibrate ||
          navigator.msVibrate;
        navigator.vibrate(100);
      }
    } catch (error) {
      console.log(error.message);
    }
  }, [clickSound]);

  React.useEffect(() => {
    window.enableVibrate = localStorage.getItem("enableVibrate") !== "false";
    window.enableAudio = localStorage.getItem("enableAudio") !== "false";
    const localRenderPeixel = localStorage.getItem("renderPeixel");
    window.renderPeixel = localRenderPeixel
      ? JSON.parse(localRenderPeixel)
      : [1, 1, 1];
    if (checkUse()) {
      initGame().then(() => {
        if (!window.game.rom["GAME.ROM"]) {
          setloading("");
        } else {
          setShowloading(false);
        }
      });
    }
    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <div className={styles.container}>
      <audio src={`${process.env.PUBLIC_URL}/click.mp3`} ref={audio} />
      <GameCenter
        show={showGameCenter}
        onCancel={() => toggleShowGameCenter(false)}
      />
      <div className={styles.cross}>
        <div id="3" className={styles.crossLeft} onClick={clickButton} />
        <div id="4" className={styles.crossRight} onClick={clickButton} />
        <div id="1" className={styles.crossUp} onClick={clickButton} />
        <div id="2" className={styles.crossDown} onClick={clickButton} />
      </div>
      <div className={styles.display}>
        {showLoading && <span className={styles.loading}>{loading}</span>}
        <canvas id="canvas" width="160" height="96" />
        <div className={styles.wolf}>BBK RPG  by-</div>
      </div>
      <div className={styles.actionButtons}>
        <div id="7" className={styles.aButton} onClick={clickButton}></div>
        <div id="8" className={styles.bButton} onClick={clickButton}></div>
      </div>
      <div id="5" className={styles.leftButton} onClick={clickButton}></div>
      <div id="6" className={styles.rightButton} onClick={clickButton}></div>
      <Setting clickSound={clickSound} />
      <div className={styles.tools}>
        <span
          className={styles.toolButton}
          onClick={() => {
            clickSound();
            setShowAlert(true);
          }}
        >
          APP
        </span>
        <Alert
          show={showAlert}
          message="QQ618027271APPAPP!"
          onCancel={() => setShowAlert(false)}
        />
        <span
          className={styles.toolButton}
          style={{ marginLeft: "20px" }}
          onClick={() => {
            clickSound();
            if (checkUse()) {
              toggleShowGameCenter(true);
            }
          }}
        >
          
        </span>
      </div>
    </div>
  );
});
